import React from 'react';
import Swiper from 'swiper';

import Hero from "./components/hero.jsx";
import ScrollToSection from "./components/scrollToSection.jsx";
import CompanyNews from "./components/companyNews.jsx";
import SolveWays from "./components/solveWays.jsx";
import Partners from "./components/partners.jsx";

export default class home extends React.Component {

  componentDidMount() {
    new Swiper('.swiper-container', {
      loop: false,  //
      slidesPerView: 'auto'
    });
  };

  render() {
    return (
      <div>
        <div className="pace  pace-inactive"><div className="pace-progress" data-progress-text="100%" data-progress={99} style={{ transform: 'translate3d(100%, 0px, 0px)' }}>
          <div className="pace-progress-inner" />
        </div>
          <div className="pace-activity" /></div>
        {/* MAIN WRAPPER */}
        <div className="body-wrap">
          <div id="st-container" className="st-container">
            <div className="st-pusher">
              <div className="st-content">
                <div className="st-content-inner">
                  {/* Slider */}
                  <Hero/>
                  {/* scrollToSection */}
                  <ScrollToSection/>
                  {/* companyNews */}
                  <CompanyNews/>
                  {/* SolveWays */}
                  <SolveWays />
                  {/* partners */}
                  <Partners/>
                </div>
              </div>
            </div>{/* END: st-pusher */}
          </div>{/* END: st-container */}
          </div>{/* END: body-wrap */}
        {/* SCRIPTS */}
        <a href="#" className="nohover" style={{textAlign:'center',lineHeight:'40px',position: 'fixed',height:'40px',width:'40px',right: '20px',bottom:'20px',borderRadius:'100%'}}>
          <i className="iconfont" style={{color:'#3A6788',fontSize:'2.5em',opacity:'.8'}}>&#xe62e;</i>
        </a>
        
      </div>
    );
  }
};


