import React from "react";
import {Map} from "react-amap";
export default class contact extends React.Component {
    constructor(){
        super();
        this.mapCenter = {longitude: 121.494933,latitude: 31.339559};
    }
  render() {
    //   console.log(BMap)
    return (
      <section className="slice sct-color-2 relative b-xs-top " style={{width: '100%', overflow:'hidden'}}>
        <div className="container">
          <span className="clearfix" />
          <span className="space-xs-xl" />
          <div className="row-wrapper">
            <div className="row">
              <div className="col-lg-12 col-md-12 col-sm-12 col-12">
                <div className="block block--style-1 list">
                  <div className="block-image">
                    <div style={{height:'500px',width:'342px'}}>
                      <Map amapkey={"c2f73e381c08297d870a8c750868208d"} zoom={16} center={this.mapCenter} />
                    </div>
                  </div>
                  <div className="block-content">
                    <div className="block-body py-1">
                      <h3 className="heading heading-5 strong-500">
                        
                      </h3>
                      <p
                        className="mt-3 c-gray-light"
                        style={{ paddingRight: "20px" }}
                      >
                        021-55887663
                      </p>
                      <p
                        className="mt-3 c-gray-light"
                        style={{ paddingRight: "20px" }}
                      >
                        1055652020@qq.com
                      </p>
                      <p
                        className="mt-3 c-gray-light"
                        style={{ paddingRight: "20px" }}
                      >
                        15A1105
                      </p>
                      <form className="form-horizontal mt-5">
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            id="name"
                            placeholder=""
                          />
                        </div>
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            id="tel"
                            placeholder=""
                          />
                        </div>
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            id="email"
                            placeholder=""
                          />
                        </div>
                        <div className="form-group">
                          <textarea placeholder="" className="form-control" rows="3"></textarea>
                        </div>
                        <div className="form-group">
                          <button type="submit" className="btn btn-primary">
                            
                          </button>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}



