import React from 'react';

class hero extends React.Component {
    render() {
        return <section className="swiper-js-container background-image-holder" style={{ height: '600px' }} data-holder-offset=".navbar">
            <div className="swiper-container swiper-container-horizontal swiper-container-fade" data-swiper-effect="fade" data-swiper-autoplay="true" data-swiper-items={1} data-swiper-space-between={0}>
                <div className="swiper-wrapper">
                    {/* Slide */}
                    <div className="swiper-slide swiper-slide-active" data-swiper-autoplay={8000} style={{ width: '100%', opacity: 1, transform: 'translate3d(0px, 0px, 0px)' }}>
                        <div className="slice holder-item holder-item-light has-bg-cover bg-size-cover" style={{ backgroundImage: `url(${require("@/assets/images/backgrounds/slider/img-341.jpg")})`, backgroundPosition: 'bottom bottom' }}>
                            <span className="mask mask-dark--style-2" />
                            <div className="container d-flex align-items-center no-padding">
                                <div className="col">
                                    <div className="row row-cols-xs-spaced align-items-center py-5 text-center text-md-left">
                                        <div className="col-md-7 col-lg-6">
                                            <h2 className="heading heading-1 strong-300 animated animation-ended" data-animation-in="fadeInDown" data-animation-delay={200} style={{ fontFamily: 'Microsoft YaHei' }}>
                                                
                          </h2>
                                            <p className="lead mt-4 animated animation-ended" data-animation-in="fadeInDown" data-animation-delay={1600}>
                                                SWMM
                                                GIS
                                                
                          </p>
                                            <a href="product.html" className="btn btn-styled btn-base-1 mt-4 animated animation-ended" data-animation-in="bounceInUp" data-animation-delay={1800}></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    
    }
}

export default hero;


