import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";

// 
import Header from "../components/header.jsx";
import Footer from "../components/footer.jsx";

// 
import Home from "../components/home/home.jsx";
import SimDemostration from "../components/simDemonstration/index.jsx";
import SuccessCase from "../components/successCase/index.jsx";
import WaterAffairs from "../components/waterAffairs/index.jsx";
import NewsDetails from "../components/newsDetails.jsx";
import Contact from "../components/contact/index.jsx";

class router extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Router>
        <Header />
        <Route exact path="/" component={Home} />
        <Route path="/newsdetails" component={NewsDetails} />
        <Route path="/simdemostration" component={SimDemostration} />
        <Route path="/successcase" component={SuccessCase} />
        <Route path="/wateraffairs" component={WaterAffairs} />
        <Route path="/contact" component={Contact} />
        <Footer />
      </Router>
    );
  }
}

export default router;



