import React from "react";
import Hero from "./home/components/hero.jsx";

export default class newsDetail extends React.Component {
  constructor() {
    super();
    (this.state = {
      companyNewsDetail: [
        {
          id: 0,
          title: "",
          author: "",
          time: "2020.1.1",
          catagory: "",
          img: "news2.jpg",
          introduce:
            "201861",
          content: [
            "BIM/FMBIMFMBIM+FM",
            ""
          ],
          tags: [""]
        },
        {
          id: 1,
          title: "",
          author: "",
          time: "2020.1.1",
          catagory: "",
          img: "news1.jpg",
          introduce:
            "",
          content: [
            "<br/>BIM/FMBIMFMBIM+FM",
            "<br/>"
          ],
          tags: [""]
        },
        {
          id: 1000,
          catagory: "",
          author:"",
          time:"2019.5.4",
          content: `       2019418

          
   
          `,
          title:
            " ",
          introduce:
            "  2019418",
          img: "img-1.jpg",
          tags: [""]
        },
        {
          id: 1001,
          catagory: "",
          author:"",
          time:"2019.5.4",
          content: `       2019431229.52%

          206023%20189303231141459/617222005500
   
          HK613620181231165.25107426.84/401.65/21.13/4/550/1887/513/
   
          `,
          title: "1229.52%",
          introduce:
            " 2019431229.52%",
          img: "img-2.jpg",
          tags: [""]
        },
        {
          id: 1002,
          catagory: "",
          author:"",
          time:"2019.5.4",
          content: `      

          2019323E202018
   
          2018
   
          2016125
   
          55002018`,
          title: "2018",
          introduce:
            " 2019323E202018",
          img: "img-5.jpg",
          tags: [""]
        }
      ]
    }),
      (this.detail = null);
  }
  componentWillMount() {
    let id = parseInt(this.props.location.pathname.split("/")[2]);
    console.log(id)
    if (id !== undefined) {
      this.detail = this.state.companyNewsDetail.filter((v, i) => {
        return v.id === id;
      })[0];
    }
  }
  render() {
    return (
      <div>
        <Hero />
        <section
          className="slice--offset sct-color-1"
          style={{ margin: "50px auto" }}
        >
          <div className="container container-xs">
            <div className="text-center">
              <h4 className="heading heading-3 strong-400 text-normal">
                {this.detail.title}
              </h4>
              <h5 className="heading heading-xs c-gray-dark text-uppercase strong-500 letter-spacing-2 mb-0 mt-1">
                {this.detail.catagory}
              </h5>
              <span className="short-delimiter short-delimiter-center short-delimiter--style-1 short-delimiter-light short-delimiter-center short-delimiter-lg" />
              <ul className="inline-links inline-links--style-2 mt-1">
                <li>{this.detail.time}</li>
                <li>by {this.detail.author}</li>
              </ul>
            </div>
            <div className="block block-post">
              <div className="block-body block-post-body">
                <div
                  className="block-image mt-5"
                  style={{ textAlign: "center" }}
                >
                  <img alt=""
                    src={require(`@/assets/images/news/${this.detail.img}`)}
                    className="rounded"
                  />
                </div>
                <div style={{margin: '30px auto'}}>{this.detail.content}</div>
                <div className="tagcloud tagcloud--style-1 clearfix">
                  {this.detail.tags}
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



