import React from "react";
import { Link } from "react-router-dom";
class TopBar extends React.Component {
  render() {
    return (
      <div className="header">
        {/* Navbar */}
        <nav
          style={{ padding: "0" }}
          className="navbar navbar-expand-lg navbar--uppercase navbar-light bg-default navbar--link-arrow"
        >
          <div className="container navbar-container">
            {/* Brand/Logo */}
            <Link className="navbar-brand" to="/">
              {/* <img src={require("@/assets/images/logo/logo-1-b.png")} alt="Boomerang"> */}
              
              <br />
              <small>Shuhui Intelligent Technology Co Ltd.</small>
            </Link>
            <div className="d-inline-block">
              {/* Navbar toggler  */}
              <button
                className="navbar-toggler hamburger hamburger-js hamburger--spring"
                type="button"
                data-toggle="collapse"
                data-target="#navbar_main"
                aria-controls="navbarsExampleDefault"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="hamburger-box">
                  <span className="hamburger-inner" />
                </span>
              </button>
            </div>
            <div
              className="collapse navbar-collapse align-items-center justify-content-end"
              id="navbar_main"
            >
              <ul className="navbar-nav" data-hover="dropdown">
                <li className="nav-item dropdown">
                  <Link to="/" className="nav-link" href="index.html">
                    
                  </Link>
                </li>
                <li className="nav-item dropdown">
                  <Link to="/wateraffairs" className="nav-link">
                    
                  </Link>
                </li>
                <li className="nav-item dropdown">
                  <Link to="/successcase" className="nav-link">
                    
                  </Link>
                </li>
                <li className="nav-item dropdown ">
                  <Link to="/simdemostration" className="nav-link">
                    
                  </Link>
                </li>
                <li className="nav-item dropdown ">
                  <Link to="/contact" className="nav-link">
                    
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default TopBar;



