import React from "react";
import Header from "./components/header.jsx";
import ProductProduce from "./components/productProduce.jsx";
import ProductScheme from "./components/productSchema.jsx";
import Character from "./components/character.jsx";

class waterAffairs extends React.Component {
  render() {
    return (
      <div>
        <div className="pace  pace-inactive">
          <div
            className="pace-progress"
            data-progress-text="100%"
            data-progress={99}
            style={{ transform: "translate3d(100%, 0px, 0px)" }}
          >
            <div className="pace-progress-inner" />
          </div>
          <div className="pace-activity" />
        </div>
        {/* MAIN WRAPPER */}
        <div className="body-wrap">
          <div id="st-container" className="st-container">
            <div className="st-pusher">
              <div className="st-content">
                <div className="st-content-inner">
                  <Header></Header>
                  <ProductProduce></ProductProduce>
                  <ProductScheme></ProductScheme>
                  <Character></Character>
                </div>
              </div>
            </div>
            {/* END: st-pusher */}
          </div>
          {/* END: st-container */}
        </div>
        {/* END: body-wrap */}
        {/* SCRIPTS */}
        <a
          href="#"
          className="nohover"
          style={{
            textAlign: "center",
            lineHeight: "40px",
            position: "fixed",
            height: "40px",
            width: "40px",
            right: "20px",
            bottom: "20px",
            borderRadius: "100%"
          }}
        >
          <i
            className="iconfont"
            style={{ color: "#3A6788", fontSize: "2.5em", opacity: ".8" }}
          >
            &#xe62e;
          </i>
        </a>
      </div>
    );
  }
}

export default waterAffairs;



