import React from "react";

export default class list extends React.Component {
  render() {
    return (
      <section className="slice-sm sct-color-1" style={{minHeight: '263px'}}>
        <div className="container ">
          <div className="row masonry">
            <div className="masonry-item col-md-6 col-lg-4 design">
              <div className="block block-image-holder">
                <div className="block-image">
                  <a href="">
                    <img alt="img" src={require("@/assets/images/case/case2.jpg")} />
                  </a>
                </div>
                <div className="block-info block-info-over block-info-over--style-1 block-info-over--dark-gradient">
                  <div className="block-info-inner">
                    <div className="text-center">
                      <a
                        href=""
                        className="heading heading-4 strong-500 mb-0 c-white"
                      >
                        
                      </a>
                      <span className="clearfix" />
                      <small className="text-uppercase c-white"></small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="masonry-item col-md-6 col-lg-4 design">
              <div className="block block-image-holder">
                <div className="block-image">
                  <a href="">
                    <img alt="img" src={require("@/assets/images/case/case3.jpg")} />
                  </a>
                </div>
                <div className="block-info block-info-over block-info-over--style-1 block-info-over--dark-gradient">
                  <div className="block-info-inner">
                    <div className="text-center">
                      <a
                        href=""
                        className="heading heading-4 strong-500 mb-0 c-white"
                      >
                        
                      </a>
                      <span className="clearfix" />
                      <small className="text-uppercase c-white"></small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}



