import React from 'react';

class productProduce extends React.Component {
    render() {
        return <section className="slice sct-color-1" id="scrollToSection">
            <div className="section-title section-title--style-1 text-center mb-4">
                <h3 className="section-title-inner text-normal">
                    
          </h3>
                <span className="section-title-delimiter clearfix d-none" />
            </div>
            <span className="clearfix" />
            <div className="fluid-paragraph c-gray-light strong-300 text-center" style={{ lineHeight: 2 }}>
            
            GISSWMM
        </div>
            <span className="space-xs-xl" />
            <div className="container">
                <div className="row-wrapper text-center">
                    <div className="row">
                        <div className="col-lg-2 col-md-4 col-sm-12">
                            <div style={{ marginBottom: '16px' }}>
                                <img alt="img" src={require("@/assets/images/icons/index/icon-7.jpg")} style={{ width: '65px', height: '52px' }} />
                            </div>
                            <div style={{ color: '#666' }}></div>
                        </div>
                        <div className="col-lg-2 col-md-4 col-sm-12">
                            <div style={{ marginBottom: '16px' }}>
                                <img alt="img" src={require("@/assets/images/icons/index/icon-8.jpg")} style={{ width: '65px', height: '52px' }} />
                            </div>
                            <div style={{ color: '#666' }}></div>
                        </div>
                        <div className="col-lg-2 col-md-4 col-sm-12">
                            <div style={{ marginBottom: '16px' }}>
                                <img alt="img" src={require("@/assets/images/icons/index/icon-9.jpg")} style={{ width: '65px', height: '52px' }} />
                            </div>
                            <div style={{ color: '#666' }}></div>
                        </div>
                        <div className="col-lg-2 col-md-4 col-sm-12">
                            <div style={{ marginBottom: '16px' }}>
                                <img alt="img" src={require("@/assets/images/icons/index/icon-10.jpg")} style={{ width: '65px', height: '52px' }} />
                            </div>
                            <div style={{ color: '#666' }}>GIS</div>
                        </div>
                        <div className="col-lg-2 col-md-4 col-sm-12">
                            <div style={{ marginBottom: '16px' }}>
                                <img alt="img" src={require("@/assets/images/icons/index/icon-11.jpg")} style={{ width: '65px', height: '52px' }} />
                            </div>
                            <div style={{ color: '#666' }}></div>
                        </div>
                        <div className="col-lg-2 col-md-4 col-sm-12">
                            <div style={{ marginBottom: '16px' }}>
                                <img alt="img" src={require("@/assets/images/icons/index/icon-12.jpg")} style={{ width: '65px', height: '52px' }} />
                            </div>
                            <div style={{ color: '#666' }}></div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    }
}

export default productProduce;


