import React from "react";

class partners extends React.Component {
    render() {
        const style = { width: '173.333px', margin: '0 20px' };
        return <section className="slice sct-color-1 b-xs-top">
            <div className="container" style={{width: '50%'}}>
                <div className="text-center">
                    <h3 className="heading heading-3 strong-500">
                        
              </h3>
                    <div className="fluid-paragraph mt-1">
                    </div>
                </div>
                <span className="space-xs-xl" />
                <div className="swiper-js-container">
                    <div className="swiper-container swiper-container-support swiper-container-horizontal" data-swiper-items="4" data-swiper-space-between={25} data-swiper-md-items={4} data-swiper-md-space-between={25} data-swiper-sm-items={3} data-swiper-sm-space-between={20} data-swiper-xs-items={2}>
                        <div className="swiper-wrapper">
                            <div className="swiper-slide swiper-slide-active" style={style}>
                                <div className="client-logo client-logo--style-4">
                                    <div className="view view-first">
                                        <a href="">
                                            <img src={require("@/assets/images/prv/clients/t55.png")} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="swiper-slide swiper-slide-next" style={style}>
                                <div className="client-logo client-logo--style-4">
                                    <div className="view view-first">
                                        <a href="">
                                            <img src={require("@/assets/images/prv/clients/t11.png")} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="swiper-slide" style={style}>
                                <div className="client-logo client-logo--style-4">
                                    <div className="view view-first">
                                        <a href="">
                                            <img src={require("@/assets/images/prv/clients/t22.png")} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="swiper-slide" style={style}>
                                <div className="client-logo client-logo--style-4">
                                    <div className="view view-first">
                                        <a href="">
                                            <img src={require("@/assets/images/prv/clients/t66.png")} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="swiper-slide" style={style}>
                                <div className="client-logo client-logo--style-4">
                                    <div className="view view-first">
                                        <a href="">
                                            <img src={require("@/assets/images/prv/clients/t44.png")} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="swiper-slide" style={style}>
                                <div className="client-logo client-logo--style-4">
                                    <div className="view view-first">
                                        <a href="">
                                            <img src={require("@/assets/images/prv/clients/t33.png")} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    }
}

export default partners;


